import React, { useState, useEffect } from 'react';
import axios from 'axios';

import { Box } from '@material-ui/core'

import Spinner from './Spinner'
import Datepicker from './Datepicker'
import DRSTable from './DRSTable'

const Dashboard = () => {

    const [info, setInfo] = useState([])
    const [date, setDate] = useState('')
    const [isLoading, setIsLoading] = useState(true)

    useEffect(() => {
        const fetchItems = async () => {
            const result =  await axios.get(`https://raw.githubusercontent.com/Dereooo/planosp/master/KPI/data/kpi.json`)

            setInfo(result.data)
            setDate(Object.keys(result.data[result.data.length - 1])[0])
            setIsLoading(false)
        }

        fetchItems()

    }, [])

    const datasDisponiveis = info.map(x => Object.keys(x)[0])
    const infoData = info.filter(x => date in x)["0"]

    const dateUnparse = (date) => {
        var d = new Date(date),
            month = '' + (d.getMonth() + 1),
            day = '' + d.getDate(),
            year = d.getFullYear();
    
        if (month.length < 2) 
            month = '0' + month;
        if (day.length < 2) 
            day = '0' + day;
    
        return [year, month, day].join('-');
    }

    const dateParse = (date) => {
        const year = date.slice(0,4);
        const month = date.slice(5,7);
        const day = date.slice(8,10);
        var d = new Date(year,month, day);    
        return d;
    }

    return isLoading ? (<Spinner />) : (
        <Box mt={3}>
            <Datepicker
            dateUnparse={dateUnparse}
            dateParse={dateParse}
            datasDisponiveis={datasDisponiveis}
            date={date}
            onDateChange = {(e) => {
                setDate(dateUnparse(e))
            }}
            />
            <DRSTable
            infoData={infoData}
            date={date}
            />
        </Box>
    )
}

export default Dashboard;
