import React from "react"
import Box from "@material-ui/core/Box"
import Paper from "@material-ui/core/Paper"
import { DatePicker } from "@material-ui/pickers"

const Datepicker = props => {
  const { date, onDateChange, datasDisponiveis, dateParse, dateUnparse } = props

  console.log(date)

  return (
    <div>
      <Box
        component={Paper}
        className="center"
        style={{ width: "250px", margin: "20px auto", padding: "20px" }}
      >
        <DatePicker 
        disableToolbar
        autoOk
        allowKeyboardControl
        variant="inline"
        label="Selecione uma data"
        format="dd/MM/yyyy"
        shouldDisableDate={(date) => (
          !(datasDisponiveis.includes(dateUnparse(date)))
        )}
        value={dateParse(date)}
        defaultValue={dateParse(date)}
        onChange={onDateChange} />
      </Box>
    </div>
  )
}

export default Datepicker
